body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div, p {
  margin: 0;
  padding: 0;
}

h1 {
  width: 80%;
  margin-left: 10%;
  font-weight: 300;
  margin-bottom: 20px;
}

.user-view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  /* align-items: center; */
  margin-bottom: 80px;
  width: 80%;
  box-shadow: 0px 0px 70px -3px rgba(207,207,207,1);
  padding: 20px;
  border-radius: 30px;
}

.user-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
}

.user-name {
  font-size: 30px;
  margin-left: 10px;
}

.user-image {
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 40px;
}

.user-handle {
  font-size: 16px;
}

.user-handle a {
  text-decoration: none;
  color: rgb(27, 149, 224);
}

.user-image img {
  width: 100%;
}

.follower-count {
  margin-bottom: 5px;
  font-size: 20px;
}

.chart-view {

}

.follower-delta {
  margin-bottom: 20px;
}

.follower-delta span {
  margin-right: 3px;
}

.follower-delta .up {
  color: green;
}

.follower-delta .down {
  color: red;
}

.date-selector {
  margin-left: 10%;
  margin-bottom: 40px;
}

.date-selector a {
  margin-right: 20px;
}
